body {
  font-family: Roboto, Verdana, Tahoma, sans-serif;
}

img {
  max-width: 370px;
  width: 100%;
  height: 100%;
}

.cat-info {
  gap: 20px;
  margin-left: 20px;
  margin-right: 20px;
  display: flex;
}

p > span {
  font-weight: 600;
}

.breed-select {
  margin: 20px;
}

.loader, .error {
  margin: 0 20px;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid #fff;
  border-style: solid solid dotted dotted;
  border-radius: 50%;
  animation: rotation 2s linear infinite;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 24px;
  height: 24px;
  transform-origin: center;
  border: 3px solid #ff3d00;
  border-bottom-style: dotted;
  border-radius: 50%;
  margin: auto;
  animation: rotationBack 1s linear infinite;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes rotationBack {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(-360deg);
  }
}

.is-hidden {
  display: none;
}

